				this.$set(this.checkedMaps, orderId, !this.checkedMaps[orderId])
				this.checkAll = Object.values(this.checkedMaps).filter(a => a).length === this.table.list.length
			},
			toggleAllChoose() {
				this.checkAll = !this.checkAll
				if (this.checkAll) {
					this.checkedMaps = this.table.list.reduce((memo, item) => {
						memo[item.id] = true
						return memo
					}, {})
				} else {
					this.checkedMaps = {}
				}
			}
